.footer {
  background-color: $color-black;
  padding: 36px 0;

  .container {
  }

  &__wrapper {
    max-width: 350px;
    margin: 0 auto;
  }

  &__body {
  }

  &__item {
  }

  &-image {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    &__link {
      display: block;
      position: relative;
      overflow: hidden;
      border-radius: 8px;
      padding-bottom: 27.5%;
      picture {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
  &-email {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20px;

    &__title {
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      color: $color-white;
      margin-bottom: 10px;
    }

    &__content {
      display: flex;
      align-items: center;
      svg {
        width: 24px;
        height: 24px;
        color: $color-gray-2;
        margin-right: 10px;
        flex: 0 0 auto;
      }
    }

    &__link {
      font-weight: 300;
      font-size: 16px;
      line-height: 140%;
      color: $color-gray-2;
      @media (any-hover: hover){
        transition: color $transition-time;
        &:hover{
          color: $color-white;
        }
      }
    }
  }
}

