.number-square-list, .check-square-list {
  list-style-type: none;
  counter-reset: num;

  li {
    position: relative;
    padding-left: 2em;
    line-height: 140%;
    color: $color-gray-5;
    font-size: 16px;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}
.number-square-list {
  li {
    &:before {
      content: counter(num);
      counter-increment: num;
      position: absolute;
      top: 0;
      left: 0;
      width: 1.5em;
      height: 1.5em;
      border-radius: 5px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-weight: 700;
      line-height: 115%;
      text-align: center;
      color: $color-white;
      background-color: #84DE46;
      font-size: 16px;
    }
  }
}
.check-square-list {
  li {
    .check-icon {
      svg {
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 5px;
        color: $color-white;
        background-color: #84DE46;
        width: 24px;
        height: 24px;
      }
    }
  }
}

@media all and (min-width: $screen-md) {
  .number-square-list, .check-square-list {
    li {
      font-size: 20px;

      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }
  .number-square-list {
    li {
      &:before {
        font-size: 18px;
      }
    }
  }
  .check-square-list {
    li {
      .check-icon {
        svg {
          width: 28px;
          height: 28px;
        }
      }
    }
  }
}


