.pagination {
  display: flex;
  justify-content: center;

  &__body {
    display: flex;
    border: 0.5px solid $color-background-1;
    border-radius: 8.6px;
  }

  &__arrow {
    display: flex;
    align-items: center;
    width: 40px;
    height: 40px;
    border: 0.5px solid $color-background-1;

    @media (any-hover: hover){
      transition: border-color $transition-time;
      svg {
        transition: color $transition-time;
      }
      &:hover {
        border-color: $color-blue-4;
        svg {
          color: $color-blue-1;
        }
      }
    }

    svg {
      width: 24px;
      height: 24px;
      color: $color-blue-4;
      margin: 0 9px;
    }
    &._prev {
      justify-content: flex-start;
      border-radius: 8px 0 0 8px;
      svg {
        transform: rotate(180deg);
      }
    }
    &._next {
      justify-content: flex-end;
      border-radius: 0 8px 8px 0;
      svg {
      }
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    &.is-active {
      .pagination__number {
        color: $color-white;
        background-color: $color-blue-4;
        border-color: $color-blue-4;

        .is-collapsed {
        }
      }
    }
  }

  &__number {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: $color-blue-4;
    border: 0.5px solid $color-background-1;

    @media (any-hover: hover){
      transition: color $transition-time,
      border-color $transition-time;
      &:hover {
        color: $color-blue-1;
        border-color: $color-blue-4;
      }
    }

    .is-collapsed {
    }
  }
}

@media all and (min-width: 360px) {
  .pagination {
    &__arrow {
      width: 60px;
    }
  }
}