.sevmorsnab-page {

  &__wrapper {
    padding-top: 25px;
    padding-bottom: 80px;
  }

  &__bread-crumbs {
    order: 1;
    margin-bottom: 10px;
  }

  &__title {
    margin-bottom: 25px;
    order: 2;
  }

  &__main {
    order: 4;

    &-text {
      margin-top: 40px;
      margin-bottom: 60px;
    }

    &-form {
      &-title {
        font-weight: 700;
        font-size: 24px;
        color: $color-gray-5;
        margin-bottom: 25px;
      }

      &-subtitle {
        color: $color-gray-3;
        font-size: 16px;
        margin-bottom: 10px;
      }

      &-input-file {
        margin-bottom: 25px;
        .input-file {}
      }

      &-inputs {
      }

      &-input {
        margin-bottom: 20px;

        .input-block {
          width: 100%;
        }
      }
      &-button {
        width: 100%;
        margin-top: 20px;
      }
    }
  }


  &__side {
    order: 3;

    &-body {
      border-radius: 8px;
      background-color: rgba($color-orange-1, .2);
      padding: 30px;
    }

    &-icon-block {
      margin-bottom: 20px;
    }

    &-icon {
      margin: 0 auto;
      border-radius: 999px;
      overflow: hidden;
      width: 160px;
      height: 160px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-title {
      font-weight: 700;
      color: $color-background-1;
      font-size: 20px;
      margin-bottom: 25px;
    }

    &-button.button {
      width: 100%;
      padding-left: 5px;
      padding-right: 5px;
      margin-top: 10px;
    }
  }
}

