.docs-page {
  &__wrapper {
    padding: 30px 0 75px;
  }
  .container {}
  &__bread-crumbs {}
  &__title {
    margin: .33em 0 1em;
  }
  &__body {
  }
  &__item-block {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  &__item {
    display: block;
    overflow: hidden;
    border-radius: 8px;
    &-zoom {
      display: none;
    }
    @media (any-hover: hover){
      position: relative;
      transition: color $transition-time;
      &-zoom {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 24px;
        height: 24px;
        color: $color-white;
        opacity: 0;
        transform: translate(-50%, -50%);
        transition: opacity $transition-time;
      }
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        background-color: rgba($color-background-2, .75);
        transition: opacity $transition-time;
      }
      &:hover {
        &:before {
          opacity: 1;
        }
        .docs-page__item-zoom {
          opacity: 1;
        }
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

@media all and (min-width: 400px) {
  .docs-page {
    &__wrapper {
      padding: 30px 0 100px;
    }
    .container {}
    &__bread-crumbs {}
    &__title {}
    &__body {}
    &__item-block {
      max-width: 50%;
    }
    &__item {
      img {}
    }
  }
}

@media all and (min-width: $screen-sm) {
  .docs-page {
    .container {}
    &__bread-crumbs {}
    &__title {}
    &__body {}
    &__item-block {}
    &__item {
      img {}
    }
  }
}

@media all and (min-width: $screen-md) {}

@media all and (min-width: $screen-lg) {
  .docs-page {
    .container {}
    &__bread-crumbs {}
    &__title {}
    &__body {}
    &__item-block {
      &:not(:last-child) {
        margin-bottom: 26px;
      }
    }
    &__item {
      img {}
    }
  }
}

@media all and (min-width: $screen-xl) {}

