.bread-crumbs {
  &__body {
    display: flex;
  }
  &__link {
    font-weight: 400;
    white-space: nowrap;
    font-size: 11px;
    &:not(:last-child) {
      padding-right: 10px;
      margin-right: 10px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 1px;
        background-color: rgba($color-white, .33);
      }
    }
    &._link-inactive {
      color: rgba($color-white, .75);
      @media (any-hover: hover){
        transition: color $transition-time;
        &:hover {
          color: rgba($color-white, 1);
        }
      }
    }

    &._link-active {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      color: rgba($color-white, .33);
    }
  }
}

@media all and (min-width: 600px) {
  .bread-crumbs {
    &__body {}
    &__link {
      font-size: 14px;
      &._link-inactive {
      }
      &._link-active {
      }
    }
  }
}

.body--theme-light {
  .bread-crumbs {
    &__body {}
    &__link {
      &:not(:last-child) {
        &:after {
          background-color: rgba($color-gray-4, .25);
        }
      }

      &._link-inactive {
        color: rgba($color-gray-4, 1);

        @media (any-hover: hover){
          transition: color $transition-time;
          &:hover {
            color: rgba($color-gray-2, 1);
          }
        }
      }
      &._link-active {
        color: rgba($color-gray-4, .33);
      }
    }
  }
}

@media all and (min-width: $screen-md) {
  .bread-crumbs {
    &__body {}
    &__link {
      font-size: 14px;
      &._link-inactive {
      }

      &._link-active {
      }
    }
  }
}

.bread-crumbs {
  &__body {}
  &__link {
    &._link-inactive {
    }

    &._link-active {
    }
  }
}
