.input-block {
  position: relative;
  &__input {
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-bottom: 1px solid $color-blue-4;
    font-size: 18px;
    font-weight: 400;
    color: $color-white;
    padding: 10px 0;
    transition: border-bottom-color $transition-time;
    &:focus {
      border-bottom: 1px solid $color-blue-1;
    }
  }
  &__lable {
    pointer-events: none;
    position: absolute;
    top: 13px;
    left: 0;
    font-size: 16px;
    font-weight: 500;
    color: $color-blue-4;
    transition: top $transition-time,
    font-size $transition-time,
    color $transition-time;
    &-req {
      color: $color-red-1;
    }
  }
  &__error-message {
    position: absolute;
    bottom: -5px;
    transform: translate(0, 100%);
    font-size: 12px;
    font-weight: 500;
    color: $color-red-2;
    display: none;
  }
}
.input-block.active {
  &__input {
  }
  .input-block__lable {
    top: -10px;
    font-size: 12px;
  }
}
.input-block.error {
  .input-block__input {
    border-bottom: 1px solid $color-red-2;
  }
  .input-block__lable {
  }
  .input-block__error-message {
    display: block;
  }
}

.body--theme-light {
  .input-block {
    &__input {
      border-bottom: 1px solid rgba($color-orange-1, .5);
      color: $color-black;
      &:focus {
        border-bottom: 1px solid rgba($color-orange-1, 1);
      }
    }
    &__lable {
      color: rgba($color-gray-3, 1);
      &-req {
      }
    }
    &__error-message {
    }
  }
  .input-block.active {
    .input-block__lable {
      color: rgba($color-orange-1, 1);
    }
  }
}