::-webkit-scrollbar {
  width: 14px;
  height: 10px;
  background: $color-background-1;
}
::-webkit-scrollbar-thumb {
  border: 3px solid $color-background-1;
  border-radius: 100px;
  background: $color-background-2;
}

::selection {
  background: $color-blue-3;
  color: $color-white;
}
::-moz-selection {
  background: $color-blue-3;
  color: $color-white;
}

.body--theme-light {
  &::-webkit-scrollbar {
    width: 14px;
    height: 10px;
    background: $color-white;
  }
  &::-webkit-scrollbar-thumb {
    border: 3px solid $color-white;
    border-radius: 100px;
    background: $color-background-1;
  }
}