.counter {
  &__wrapper {
  }

  &__title {
    font-weight: 500;
    color: $color-blue-3;
    font-size: 16px;
    margin-bottom: 10px;
  }

  &__body {
    display: flex;
    align-items: center;
  }

  &__input {
    display: flex;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid rgba($color-blue-2, .5);

    & > * {
      width: 48px;
      height: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: transparent;
    }

    &-plus, &-minus {
      svg {
        width: 18px;
        height: 18px;
        color: $color-blue-2;
        transition: color $transition-time, opacity $transition-time;
      }
      @media (any-hover: hover){
        transition: background-color $transition-time;

        &:not(.disabled) {
          &:hover {
            background-color: $color-blue-3;
            svg {
              color: $color-white;
            }
          }
        }
      }
    }

    &-minus {
      &.disabled {
        cursor: inherit;
        svg {
          opacity: .33;
        }
      }
    }

    &-input {
      text-align: center;
      font-weight: 500;
      font-size: 18px;
      color: $color-gray-1;
    }

    &-plus {
    }
  }

  &__label {
    font-weight: 400;
    font-size: 16px;
    color: $color-blue-4;
    margin-left: 12px;
  }
}


@media all and (min-width: $screen-md) {
  .counter {
    &__wrapper {
    }

    &__title {
      font-size: 18px;
      margin-bottom: 20px;
    }

    &__body {
    }

    &__input {

      & > * {
      }

      &-plus, &-minus {
        svg {
        }

        @media (any-hover: hover) {

          &:not(.disabled) {
            &:hover {
              svg {
              }
            }
          }
        }
      }

      &-minus {
        &.disabled {
          svg {
          }
        }
      }

      &-input {
      }

      &-plus {
      }
    }

    &__label {
    }
  }
}