.result-item {
  background-color: rgba($color-blue-4, .1);
  padding: 20px;
  border-radius: 8px;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  @media (any-hover: hover){
    transition: background-color $transition-time;
    &:hover {
      background-color: rgba($color-blue-4, .25);
      .result-item__buttons {
        opacity: 1;
      }
    }
  }

  &__texts {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  &__texts-title {
    flex: 0 1 71.428%;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: $color-white;
  }

  &__texts-price {
    flex: 0 1 27.143%;
    font-weight: 700;
    font-size: 18px;
    color: $color-white;
    text-align: end;
  }

  &__buttons {
    @media (any-hover: hover){
      opacity: .5;
      transition: opacity $transition-time;
    }
  }

  &__button {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &._cart-btn {
    }

    &._provider-btn {
    }

    svg {
    }

    span {
    }
  }
}


