.cart-page {
  &__body {
    padding-top: 25px;
    padding-bottom: 60px;
  }
  .container {}
  .ui-search {
    margin-bottom: 30px;
  }
  .bread-crumbs {
    margin-bottom: 10px;
  }
  &__title {
    margin-bottom: 15px;
  }
  &__info {
    display: flex;
    align-items: flex-start;
    font-weight: 500;
    color: $color-red-2;
    margin-bottom: 25px;
    font-size: 16px;

    svg {
      flex: 0 0 auto;
      width: 24px;
      height: 24px;
      margin-right: 12px;
    }
  }
  &__items {
    margin-bottom: 30px;
  }
  .result-item {}
  .pagination {}
}

