.plotniskie-uslugi-page {

  &__wrapper {
    padding-top: 25px;
  }

  &__subtitle {
    font-weight: 500;
    color: $color-gray-5;
    margin-bottom: 20px;
    font-size: 20px;
  }

  &__head {
    margin-bottom: 40px;

    &-bread-crumbs {
      margin-bottom: 10px;
    }

    &-title {
      margin-bottom: 25px;
    }

    &-body {

      &-main {
        margin-top: 40px;
        order: 2;

        &-block {
          &:not(:last-child) {
            margin-bottom: 30px;
          }
        }

        &-title {
        }

        &-list {
        }

        &-item {
        }
      }


      &-side {
        order: 1;

        &-wrapper {
          border-radius: 8px;
          background-color: #D1F3BA;
          padding: 30px;
        }

        &-title {
          font-weight: 700;
          color: $color-background-1;
          font-size: 20px;
          margin-bottom: 25px;
        }

        &-contacts {
          border-bottom: 1px solid rgba(#509B1C, .5);
          margin-bottom: 25px;

          &-block {
          }
        }

        &-form {

          &-title {
            font-weight: 400;
            line-height: 140%;
            color: $color-background-1;
            font-size: 16px;
            margin-bottom: 5px;
          }

          &-input {
            margin-bottom: 20px;
            .input-block {
              &__input {
                width: 100%;
                border-bottom: 1px solid rgba($color-background-1, 0.25) !important;
              }
              &__lable {
                color: $color-background-1 !important;
              }
              &.active {
                .input-block__input {
                  border-bottom: 1px solid rgba($color-background-1, 1) !important;
                }
              }
            }
          }

          &-button {
            width: 100%;
            margin-top: 20px;
          }
        }
      }
    }
  }


  &__gallery {

    &-title {
    }

    &-body {
      display: flex;
      flex-wrap: wrap;
    }

    &-item {
      flex: 0 1 50%;
      position: relative;

      &-wrapper {
        overflow: hidden;
        position: relative;
        width: 100%;
        padding-bottom: 100%;
        display: flex;
        justify-content: center;
      }

      &-body {
        position: absolute;
        top: 0;
        left: 0;
        min-width: 100%;
        min-height: 100%;
        background-color: #f00;
        object-fit: cover;
      }

      &-zoom {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 24px;
        height: 24px;
        color: $color-white;
        opacity: 0;
        transform: translate(-50%, -50%);
        transition: opacity $transition-time;
        z-index: 3;
        pointer-events: none;
      }
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        background-color: rgba($color-background-2, .75);
        transition: opacity $transition-time;
        z-index: 1;
        pointer-events: none;
      }
      &:hover {
        &:before {
          opacity: 1;
        }
        .plotniskie-uslugi-page__gallery-item-zoom {
          opacity: 1;
        }
      }
    }
  }
}

