.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 800;
  .container {
  }
  &__body {
    transition: padding $transition-time;
    padding: 18px 0;
    border-bottom: 1px solid rgba($color-white, .25);
  }
  &__logo {
    height: 50px;

    &-block {
      min-width: 100%;
    }
  }
  &__info {
    &-block {
      display: flex;
      align-items: center;
      flex-direction: column;
      min-width: 100%;
    }
    &-text {
      display: block;
      margin: 20px 0 15px;
      font-size: 13px;
      line-height: 1.4em;
      color: $color-white;
      b {
        font-weight: 800;
      }
    }
    &-btn {
      margin: 0 0 0 auto;
      width: 100%;
      &._cart-btn {
        position: absolute;
        top: 18px;
        right: 22px;
        font-size: 15px;
        margin-left: 15px;
        width: 40px;
        padding: 0;
        svg {
          margin-right: 0;
        }
        span {
          display: none;
        }
      }
    }
  }
}

.body--theme-light {
  .header {
    &.scrolling {
      background-color: $color-white;
    }
    .container {
    }
    &__body {
      border-bottom: 1px solid rgba($color-gray-2, .45);
    }
    &__logo {
      &-block {}
      &-text {}
    }
    &__info {
      &-block {
      }
      &-text {
        b {
        }
      }
      &-btn {}
    }
  }
}

@media all and (min-width: 360px) {
  .header-cart {
    .header__info-btn {
      &._cart-btn {
        width: 170px;
        svg {
          margin-right: 8px;
        }
        span {
          display: inline;
        }
      }
    }
  }
}

@media all and (min-width: 440px) {
  .header {
    .container {
    }
    &__body {
    }
    &__logo {

      &-block {
        min-width: auto;
      }
    }
    &__info {
      &-block {
        min-width: auto;
        flex-direction: row;
      }
      &-text {
        display: none;
      }
      &-btn {
        width: auto;
      }
    }
  }
  .header-cart {
    .header__info-btn {
      &._cart-btn {
        position: static;
        width: auto;
        padding: 0 7px;
        svg {
          margin-right: 0;
        }
        span {
          display: none;
        }
      }
    }
  }
}

@media all and (min-width: 700px) {
  .header-cart {
    .header__info-btn {
      &._cart-btn {
        svg {
          margin-right: 8px;
        }
        span {
          display: inline;
        }
      }
    }
  }
}

@media all and (min-width: $screen-sm) {
  .header {
    .container {}
    &__body {
      padding: 24px 0;
    }
    &__logo {
      height: 90px;
      &-block {}
    }
    &__info {
      &-block {}
      &-text {
        display: block;
        text-align: right;
        margin: 0 18px 0 0;
        font-size: 16px;
      }
      &-btn {
      }
    }
  }

  .header-cart {
    .header__info-text {
      display: none;
    }
    .header__info-btn {
      &._cart-btn {
        padding: 0 20px;
        svg {
        }
        span {
        }
      }
    }
  }
}

@media all and (min-width: $screen-md) {
  .header {
    position: fixed;
    transition: background-color $transition-time;
    &.scrolling {
      background-color: $color-background-2;
      .header__body {
        padding: 12px 0;
      }
    }
    .container {
    }
    &__body {
    }
    &__logo {

      &-block {}
    }
    &__info {
      &-block {
      }
      &-text {
        font-size: 18px;
        line-height: 1.5em;
      }
      &-btn {}
    }
  }

  .header-cart {
    .header__info-text {
      display: block;
    }
  }
}

@media all and (min-width: $screen-lg) {}

@media all and (min-width: $screen-xl) {}