html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-size: 16px;
  line-height: 1.5;
  font-family: "Geometria", sans-serif;
  background-color: $color-background-2;

  &.lock {
    overflow: hidden;
    touch-action: none;
  }
  &.body--theme-light {
    background-color: $color-white;
  }
}

* {
  outline: 0;
  font-family: inherit;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

input,
textarea,
button {
  border: none;
  padding: 0;
  -webkit-appearance: none;
}

button {
  cursor: pointer;
}

input {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &[type='number'] {
    -moz-appearance: textfield;
  }

  &[type='search']::-webkit-search-decoration,
  &[type='search']::-webkit-search-cancel-button,
  &[type='search']::-webkit-search-results-button,
  &[type='search']::-webkit-search-results-decoration {
    -webkit-appearance: none;
  }
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

svg {
  width: 100%;
  height: 100%;
}

ul li {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.responsive-video {
  position: relative;
  padding: 56.25% 0 0;

  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;

  &__main {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;

    & > .pt-max {
      padding-top: 220px;
      @media all and (min-width: 440px) {
        padding-top: 90px;
      }
      @media all and (min-width: $screen-sm) {
        padding-top: 140px;
      }
      @media all and (min-width: $screen-md) {
        padding-top: 160px;
      }
      @media all and (min-width: $screen-lg) {
        padding-top: 140px;
      }
      @media all and (min-width: $screen-xl) {
      }
    }
    & > .pt-min {
      padding-top: 90px;
      @media all and (min-width: 440px) {
      }
      @media all and (min-width: $screen-sm) {
        padding-top: 140px;
      }
      @media all and (min-width: $screen-md) {
      }
      @media all and (min-width: $screen-lg) {
      }
      @media all and (min-width: $screen-xl) {
      }
    }
  }
}

p {
  margin: 0 0 1em;

  &.reset-m {
    margin: 0;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 1.3;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  //margin: 1.2em 0 0.6em;

  &.reset {
    margin: 0;

    &-mt {
      margin-top: 0;
    }

    &-mb {
      margin-bottom: 0;
    }
  }
}

h1,
.h1 {
  font-weight: 500;
  font-size: 25px;
  color: $color-white;

  @media all and (min-width: $screen-sm) {
    font-size: 36px;
  }

  @media all and (min-width: $screen-md) {}

  @media all and (min-width: $screen-lg) {}

  @media all and (min-width: $screen-xl) {}
}
h2,
.h2 {
  font-size: 23px;
}
h3,
.h3 {
  font-size: 21px;
}
h4,
.h4 {
  font-size: 19px;
}
h5,
.h5 {
  font-size: 17px;
}
h6,
.h6 {
  font-size: 15px;
}

.width-full {
  width: 100%;
}
