.home-page {
  &__background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../img/index/bg.jpg") center/cover no-repeat;
    z-index: -1;
  }

  &__body {
    padding: 50px 0;
    max-width: 1270px;
    margin: 0 auto;
  }

  &__title {
    font-weight: 500;
    color: $color-white;
    margin-bottom: 10px;
    font-size: 13px;
    span {
      color: $color-blue-3;
    }
  }

  &__banner {
    overflow: hidden;
    border-radius: 8px;
    margin-top: 15px;
  }

  &__banner-title {
  }
}

