.side-contacts-block {
  margin-bottom: 20px;

  &__head {
    display: flex;
    align-items: center;
    color: $color-background-1;
    font-size: 16px;
    margin-bottom: 5px;
    svg {
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }
    span {
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__body-link {
    font-weight: 500;
    color: $color-background-1;
    font-size: 18px;
    &:not(:last-child) {
      margin-bottom: 5px;
    }
    @media (any-hover: hover){
      transition: color $transition-time;
      &:hover {
        color: rgba($color-background-1, .8);
      }
    }
  }
}

@media all and (min-width: $screen-md) {
  .side-contacts-block {
    margin-bottom: 35px;

    &__head {
      font-size: 18px;
      margin-bottom: 10px;
      svg {
      }
      span {
      }
    }

    &__body {
    }

    &__body-link {
      font-size: 24px;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
      @media (any-hover: hover){
        &:hover {
        }
      }
    }
  }
}

