.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: opacity $transition-time, visibility $transition-time;
  background: url("../img/popup/bg.jpg") center/cover no-repeat;
  z-index: 900;
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup__content {
  opacity: 1;
}

.popup__wrapper {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 12px;
  transition: visibility $transition-time;
}
.popup__content {
  color: $color-white;
  padding: 40px;
  position: relative;
  transition: opacity $transition-time;
  opacity: 0;
  border-radius: 10px;
}
.popup__close {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  color: $color-blue-4;
  border: 1px solid $color-background-1;
  border-radius: 8px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  @media all and (min-width: $screen-md) {
    width: 50px;
    height: 50px;
  }
  svg {
    width: 24px;
    height: 24px;
  }
  @media (any-hover: hover){
    transition: background-color $transition-time;
    &:hover {
      background-color: $color-background-1;
    }
  }
}
.popup__body {
  max-width: 880px;
  margin: -60px auto 0;
  padding-top: 60px;
}
.popup .container {
  position: relative;
}

.popup--form {
  .popup__wrapper {}
  .popup__content {}
  .container {}
  .popup__close {
    svg {}
  }
  .popup__body {}
  .popup__title {
    font-weight: 700;
    line-height: 140%;
    margin-bottom: 40px;
    font-size: 20px;
  }
  .popup__form {}
  .popup__input-block {
    margin-bottom: 30px;
  }
  .popup__input-title {
    font-weight: 400;
    margin-bottom: 15px;
    font-size: 16px;
  }
  .input-block {
    margin-bottom: 20px;
    width: 100%;
  }
  .popup__inputs {}
  .popup__button {
    margin-top: 20px;
    width: 100%;
  }
}

@media all and (min-width: $screen-sm) {
  .popup--form {
    .popup__wrapper {}
    .popup__content {}
    .container {}
    .popup__close {
      svg {}
    }
    .popup__body {}
    .popup__title {}
    .popup__form {}
    .popup__input-block {}
    .popup__input-title {}
    .popup__inputs {
      display: flex;
      justify-content: space-between;
    }
    .input-block {
      flex: 0 1 33%;
      margin: 0 30px 0 0;
    }
    .popup__button {
      flex: 0 1 33%;
      margin: 0;
    }
  }
}

@media all and (min-width: $screen-md) {
  .popup--form {
    .popup__wrapper {}
    .popup__content {}
    .container {}
    .popup__close {
      svg {}
    }
    .popup__body {}
    .popup__title {
      margin-bottom: 80px;
      font-size: 36px;
    }
    .popup__form {}
    .popup__input-block {
      margin-bottom: 60px;
    }
    .popup__input-title {
      margin-bottom: 20px;
      font-size: 24px;
    }
    .popup__inputs {}
    .input-block {
    }
    .popup__button {
      font-size: 18px;
      padding: 16px 55px;
    }
  }
}

@media all and (min-width: $screen-lg) {}

@media all and (min-width: $screen-xl) {}

.popup--form {
  .popup__wrapper {}
  .popup__content {}
  .container {}
  .popup__close {
    svg {}
  }
  .popup__body {}
  .popup__title {}
  .popup__form {}
  .popup__input-block {}
  .popup__input-title {}
  .popup__inputs {}
  .popup__button {}
}
