.ui-search {
  overflow: hidden;
  border-radius: 8px;
  display: flex;
  &__input-block {
    position: relative;
    flex: 1 1 auto;
  }
  &__input {
    display: block;
    width: 100%;
    height: 100%;
    padding: 15px 16px;
    font-weight: 500;
    font-size: 16px;
    color: $color-gray-4;

    &::placeholder {
      font-weight: 300;
    }
  }
  &__micro {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 28px + (2 * 12px);
    height: 100%;
    background-color: transparent;
  }
  img {
    width: 28px;
    height: 28px;
    object-fit: contain;
  }
  &__button {
    display: none;
    border-radius: 0;
    svg {}
  }
}

@media all and (min-width: $screen-sm) {
  .ui-search {
    &__input-block {}
    &__input {
      padding: 20px 30px;
    }
    &__micro {}
    img {}
    &__button {
      display: inline-flex;
      height: 60px;
      svg {}
    }
  }
}

@media all and (min-width: $screen-md) {
  .ui-search {
    &__input-block {}
