.card-page {

  &__wrapper {
    padding-top: 30px;
    padding-bottom: 80px;
  }

  .container {
  }

  .bread-crumbs {
    margin-bottom: 25px;
  }

  &__main {
    flex: 1 1 auto !important;
  }

  &__info-block {
    padding: 20px;
    border-radius: 8px;
    background-color: rgba($color-white, .05);
    line-height: 100%;
    margin-bottom: 25px;
  }

  &__info-name {
    font-weight: 700;
    line-height: 130%;
    font-size: 16px;
    margin-bottom: 30px;
  }

  &__info-item {
    flex: 0 0 auto;
    display: flex;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__info-item-name {
    font-weight: 400;
    font-size: 18px;
    color: rgba($color-white, .5);
    margin-right: 8px;
  }

  &__info-item-value {
    font-weight: 500;
    font-size: 18px;
    color: rgba($color-white, 1);
  }

  &__contacts-block {
    padding: 20px;
    border-radius: 8px;
    border: 1px solid $color-blue-4;
    background-color: rgba($color-blue-4, .25);
    line-height: 100%;
    margin-bottom: 40px;
    & > *:not(:last-child) {
      padding-right: 10px;
    }
  }

  &__contacts-company {
    font-weight: 500;
    font-size: 24px;
    color: $color-blue-2;
    margin-bottom: 20px;
    line-height: 130%;
  }

  &__contacts-item {
    flex: 0 0 auto;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__contacts-item-title {
    font-weight: 400;
    font-size: 16px;
    color: $color-blue-1;
    margin-bottom: 10px;
  }

  &__contacts-item-link {
    font-weight: 500;
    font-size: 18px;
    color: $color-blue-1;

    @media (any-hover: hover){
      transition: color $transition-time;
      &:hover {
        color: $color-white;
      }
    }
  }

  &__form-block {
  }

  &__form-title {
    font-weight: 500;
    color: $color-white;
    font-size: 18px;
    margin-bottom: 25px;
  }

  &__form-sutitle {
    font-weight: 500;
    font-size: 16px;
    color: $color-blue-3;
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 16px;
  }

  .input-file {
    background-color: transparent;
    margin-bottom: 20px;
  }

  &__form-inputs {
  }

  .input-block {
    width: 100%;
    margin-bottom: 20px;
  }

  &__button.button {
    margin-bottom: 40px;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 20px;
  }

  &__side {
    flex: 0 1 auto !important;

    &-btns {
      display: flex;
      font-weight: 500;
      font-size: 16px;
      color: rgba($color-white, .5);
      border-bottom: 1px solid rgba($color-blue-4, .5);
      margin-left: -50vw;
      margin-right: -50vw;
      margin-bottom: 25px;
      padding-left: 50vw;
      padding-right: 50vw;
    }

    &-btn {
      transition: color $transition-time;
      padding-bottom: 10px;
      position: relative;
      top: 1px;
      &.active {
        color: rgba($color-white, 1);
        border-bottom: 1px solid $color-blue-4;
      }
      &:not(:last-child) {
        margin-right: 15px;
      }
    }

    &-tabs {
    }

    &-tab {
      display: none;

      &.active {
        display: block;
      }
      &-title {
        font-weight: 500;
        font-size: 24px;
        color: $color-white;
        margin-bottom: 20px;
        display: none;
      }

      &-body {
        font-weight: 300;
        font-size: 16px;
        line-height: 170%;
        color: $color-white;
        p {
          margin: 0;
          &:not(:last-child) {
            margin-bottom: 15px;
          }
        }
      }

      &-block {
        background: rgba($color-background-1, 0.2);
        border: 1px solid $color-blue-4;
        border-radius: 8px;
        padding: 20px;
        margin-bottom: 15px;

        &-title {
          font-weight: 400;
          font-size: 18px;
          color: $color-blue-4;
          margin-bottom: 20px;
        }

        &-list {
          padding-left: 1.2em;
        }

        &-item {
          font-weight: 300;
          font-size: 16px;
          color: $color-white;

          &:not(:last-child) {
            margin-bottom: 10px;
          }
          b {
            font-weight: 500;
          }
        }
      }
    }
  }
}

