.button {
  display: inline-flex;
  flex: 0 0 auto;
  font-weight: 500;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  border-radius: 8px;
  padding: 0 30px;
  font-size: 16px;
  height: 40px;
  line-height: 1em;
  &--type {
    &-filled {}
    &-contour {

    }
  }
  &--color {
    &-blue {
      &.button--type {
        &-filled {
          color: $color-white;
          background-color: $color-blue-3;
          @media (any-hover: hover){
            transition: background-color $transition-time;
            &:hover{
              background-color: $color-blue-hover;
            }
          }
        }

        &-contour {
          color: $color-blue-2;
          border: 1px solid $color-blue-2;
          background-color: transparent;

          @media (any-hover: hover){
            transition: background-color $transition-time,
                        color $transition-time,
                        border $transition-time,;
            &:hover{
              background-color: $color-blue-3;
              color: $color-white;
              border: 1px solid $color-blue-3;
            }
          }
        }
      }
    }
    &-orange {
      &.button--type {
        &-filled {
          color: $color-white;
          background-color: $color-orange-1;
          @media (any-hover: hover){
            transition: background-color $transition-time;
            &:hover{
              background-color: rgba($color-orange-1, .7);
            }
          }
        }

        &-contour {
          color: $color-orange-1;
          border: 1px solid $color-orange-1;
          background-color: transparent;

          @media (any-hover: hover){
            transition: background-color $transition-time,
            color $transition-time,
            border $transition-time,;
            &:hover{
              background-color: $color-orange-1;
              color: $color-white;
              border: 1px solid $color-orange-1;
            }
          }
        }
      }
    }
    &-midnight {
      &.button--type {
        &-filled {
          color: $color-white;
          background-color: $color-background-1;
          @media (any-hover: hover){
            transition: background-color $transition-time;
            &:hover{
              background-color: $color-background-2;
            }
          }
        }

        &-contour {
          color: $color-background-1;
          border: 1px solid $color-background-1;
          background-color: transparent;

          @media (any-hover: hover){
            transition: background-color $transition-time,
            color $transition-time,
            border $transition-time,;
            &:hover{
              background-color: $color-background-1;
              color: $color-white;
              border: 1px solid $color-background-1;
            }
          }
        }
      }
    }
    &-dark-blue {
      &.button--type {
        &-filled {
          color: $color-white;
          background-color: $color-blue-4;
          @media (any-hover: hover){
            transition: background-color $transition-time;
            &:hover{
              background-color: $color-blue-hover;
            }
          }
        }

        &-contour {
          color: $color-blue-4;
          border: 1px solid $color-blue-4;
          background-color: transparent;

          @media (any-hover: hover){
            transition: background-color $transition-time,
            color $transition-time,
            border $transition-time,;
            &:hover{
              background-color: $color-blue-4;
              color: $color-white;
              border: 1px solid $color-blue-4;
            }
          }
        }
      }
    }
  }
  &--icon {
    &-left, &-right {
      svg {
        width: 24px;
        height: 24px;
      }
    }
    &-left {
      svg {
        margin-right: 8px;
      }
    }
    &-right {
      svg {
        margin-left: 8px;
      }
    }
  }
}

