.input-file {
  border: 1px dashed $color-blue-4;
  background-color: rgba(#122233, 1);
  border-radius: 8px;
  position: relative;
  padding: 24px;
  display: flex;

  @media (any-hover: hover){
    transition: color $transition-time,
    background-color $transition-time,
    border $transition-time;
    &:hover {
      color: $color-blue-2;
      background-color: rgba(#122233, .33);
      border: 1px dashed $color-blue-3;
    }
  }

  &__label {}
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    opacity: 0;
    display: block;
    width: 100%;
    height: 100%;
  }
  &__body {
    display: flex;
    align-items: center;
  }
  &__icon {
    flex: 0 0 auto;
    height: 55px;
    margin-right: 24px;
    width: 70px;
    svg {
      color: $color-blue-4;
    }
  }
  &__text {
    p {
      margin-bottom: 16px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__text-pc {
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: $color-gray-3;
    display: none;
  }
  &__text-mb {
    font-weight: 400;
    font-size: 16px;
    color: $color-blue-3;
    cursor: pointer;
    @media (any-hover: hover){
      transition: color $transition-time;
      &:hover {
        color: $color-blue-2;
      }
    }
  }
}

.body--theme-light {
  .input-file {
    border: 1px dashed $color-orange-1;
    background-color: rgba($color-gray-1, .33);

    &__label {}
    &__input {}
    &__body {}
    &__icon {
      svg {
        color: $color-orange-1;
      }
    }
    &__text {}
    &__text-pc {
      font-weight: 400;
      color: $color-gray-4;
      line-height: 140%;
    }
    &__text-mb {
      color: rgba($color-orange-1, 1);
      @media (any-hover: hover){
        transition: color $transition-time;
        &:hover {
          color: rgba($color-orange-1, .7);
        }
      }
    }
  }
