  @media all and (min-width: $screen-lg) {
    .input-file {
      padding: 55px 90px;

      &__label {}
      &__input {}
      &__body {}
      &__icon {
        width: 140px;
        svg {
        }
      }
      &__text {}
      &__text-pc {
      }
      &__text-mb {
      }
    }
  }
}

@media all and (min-width: 500px) {
